import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import './App.css';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function App() {
  const [file, setFile] = useState(null);
  const [summary, setSummary] = useState(null);
  const [history, setHistory] = useState([]);
  const [error, setError] = useState(null);
  const [message, setMessage] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchHistory();
  }, []);

  const fetchHistory = async () => {
    try {
      const response = await axios.get('/api/history/');
      setHistory(response.data);
    } catch (err) {
      console.error("Failed to fetch history", err);
    }
  };

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
    setError(null);
    setMessage(null);
  };

  const handleUpload = async (e) => {
    e.preventDefault();
    if (!file) {
      setError("Please select a CSV file first.");
      return;
    }

    const formData = new FormData();
    formData.append('file', file);

    setLoading(true);
    setError(null);
    setMessage(null);
    setSummary(null);

    try {
      const response = await axios.post('/api/upload/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      setSummary(response.data);
      setMessage("Upload successful!");
      fetchHistory(); // Refresh history
    } catch (err) {
      if (err.response && err.response.data && err.response.data.error) {
        setError(err.response.data.error);
      } else {
        setError("An unexpected error occurred during upload.");
      }
    } finally {
      setLoading(false);
    }
  };

  const handleDownloadReport = () => {
     window.location.href = '/api/report/';
  };

  const chartData = summary ? {
    labels: Object.keys(summary.type_distribution),
    datasets: [
      {
        label: 'Equipment Count',
        data: Object.values(summary.type_distribution),
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
      },
    ],
  } : null;

  return (
    <div className="App">
      <header className="App-header">
        <h1>Chemical Equipment Parameter Visualizer</h1>
      </header>
      
      <main className="container">
        <section className="upload-section">
          <h2>Upload CSV</h2>
          <form onSubmit={handleUpload}>
            <input type="file" accept=".csv" onChange={handleFileChange} />
            <button type="submit" disabled={loading}>
              {loading ? 'Uploading...' : 'Upload'}
            </button>
          </form>
          {error && <div className="error-message">{error}</div>}
          {message && <div className="success-message">{message}</div>}
        </section>
        
        {summary && (
          <section className="summary-section">
            <div className="summary-header">
                <h2>Analysis Summary</h2>
                <button onClick={handleDownloadReport} className="report-btn">Download PDF Report</button>
            </div>
            
            <div className="cards-container">
              <div className="card">
                <h3>Total Equipment</h3>
                <p>{summary.total_count}</p>
              </div>
              <div className="card">
                <h3>Avg Flowrate</h3>
                <p>{summary.avg_flowrate}</p>
              </div>
              <div className="card">
                <h3>Avg Pressure</h3>
                <p>{summary.avg_pressure}</p>
              </div>
              <div className="card">
                <h3>Avg Temp</h3>
                <p>{summary.avg_temperature}</p>
              </div>
            </div>

            <div className="chart-container">
              <h3>Type Distribution</h3>
              <div className="chart-wrapper">
                 <Bar data={chartData} options={{ responsive: true, maintainAspectRatio: false }} />
              </div>
            </div>
          </section>
        )}

        <section className="history-section">
          <h2>Upload History (Last 5)</h2>
          {history.length === 0 ? (
            <p>No history available.</p>
          ) : (
            <table>
              <thead>
                <tr>
                  <th>File Name</th>
                  <th>Date</th>
                  <th>Total Equipment</th>
                </tr>
              </thead>
              <tbody>
                {history.map((item, index) => (
                  <tr key={index}>
                    <td>{item.file_name}</td>
                    <td>{new Date(item.uploaded_at).toLocaleString()}</td>
                    <td>{item.total_equipment}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </section>
      </main>
    </div>
  );
}

export default App;
